	import {uniCollapse,uniCollapseItem} from "@dcloudio/uni-ui"
	import avatar from "../../yq-avatar.vue";
	import prompt from '../../prompt.vue';
	export default {
		data() {
			return {
                index: "",
				array:[],
				array1:[],
				name:'',
				phone:'',
				address:'',
				network:'',
				Id:"",
				sketch:'',
				branch:'',
				pull:'',
				branch_name:'',
				nots:'',
				userName:'',
				camp:'',
				shrink:'',
				primary:'',
				contract_name:'',
				job:'',
				smallPrimary:'',
				
				
				selectValue:"",   
				imageList:[],
				date: "",
				list:[],
				pic:[],
				
				
				
				
			}
		},
		onLoad:function(option){
			var _self = this
			_self.Id = option.id
			_self.getKindList()	
			if(!uni.getStorageSync('componyId')|| uni.getStorageSync('componyId')==''){
					uni.setStorageSync("componyId","0") 
			}
			
		},
		onShow:function(){
			
		},
		methods: {
			/**
			 * @param {Object} e缩略图
			 */
			doBefore() {
				console.log('doBefore');
			},
			clk(index) {
				this.$refs.avatar.fChooseImg(index,{
					selWidth: '300upx', selHeight: '300upx', 
					expWidth: '200upx', expHeight: '200upx',
					inner: index ? 'true' : 'false'
				});
			},
			doUpload(rsp) {
				var _self = this
				console.log(rsp);
				//this.$set(this.urls, rsp.index, rsp.path);
				uni.uploadFile({
					url: _self.$api+'dockingManager/upload', //仅为示例，非真实的接口地址
					//url: "http://www.tp5.com/index", //仅为示例，非真实的接口地址
					filePath: rsp.path,
					name: 'file', 
					formData: {
						'user': 'test'
					},
					success: (uploadFileRes) => {
						console.log(uploadFileRes)
						_self.smallPrimary = uploadFileRes.data.replace("\"","").replace("\"","")
					}
				  }); 
			},
			previewImage: function(e) {
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			},
			getKindList:function(){
				var _self = this
				uni.request({
					url:_self.$api+'dockingManager/branchQuery',
					success:function(res){
						var array=[]
						var array1=[]
						for(var i = 0;i<res.data.length;i++){
							array[i] =  res.data[i].name
							array1[i] = res.data[i].id
						}
						_self.array = array
						_self.array1 = array1
						_self.getInfo()
					}
				})
				
			},
			bindPickerChange: function(e) {
				var _self = this
				console.log('picker发送选择改变，携带值为', e.target.value)
				this.index = e.target.value 
				this.pull = _self.array1[_self.index]
				this.branch_name =  _self.array[_self.index]
			},
				getInfo:function(){
					var _self =this
					 uni.request({
					 	url:_self.$api+"dockingManager/tradeQuery", 
					 	data:{id:_self.Id}, 
						method:"GET",
						success:function(res){
							console.log(res)
							_self.name = res.data[0].name
							_self.address = res.data[0].address
							_self.network = res.data[0].network
							_self.phone = res.data[0].phone
							_self.sketch = res.data[0].sketch
							_self.branch = res.data[0].branch
							_self.pull = res.data[0].branch
							_self.index = _self.array1.indexOf(_self.branch) 
							console.log(_self.index)
							_self.branch_name = _self.array[_self.index]
							_self.nots = res.data[0].nots
							_self.userName = res.data[0].userName
							_self.contract_name = res.data[0].userName
							_self.job = res.data[0].userPost
							_self.smallPrimary = res.data[0].smallPrimary
							_self.camp = res.data[0].camp
							if(res.data[0].smallPrimary==''||res.data[0].smallPrimary==undefined|| res.data[0].smallPrimary == null){
								_self.smallPrimary = '../../../static/img/timg1.jpg'
							}else{ 
								_self.smallPrimary = res.data[0].smallPrimary.replace(/\"/g, "");
							}
							if(res.data[0].primary==''||res.data[0].primary==undefined|| res.data[0].primary == null){
								_self.primary = '../../../static/img/timg1.jpg'
							}else{ 
								_self.primary = res.data[0].primary.replace(/\"/g, "");
							}
							if(res.data[0].shrink==''||res.data[0].shrink==undefined || res.data[0].shrink == null){
								_self.shrink = '../../../static/img/timg1.jpg' 
							}else{
								_self.shrink = res.data[0].shrink.replace(/\"/g, "");
							}
							console.log(res.data[0].shrink)
							console.log(res.data[0].primary)
							// console.log(_self.imageList)
							// if(_self.imageList==''||_self.imageList==undefined){
							// 	_self.imageList = []
							// }else{
							// 	_self.imageList = res.data[0].shrink.split(',')
							// }
							
	
						}
					 })
				}, 
				/**
				 * 修改企业
				 */
				
				EditInfo:function(){
					var _self =this
					var reg = /[\u4E00-\u9FA5\uF900-\uFA2D]/;
					console.log(reg.test(_self.network))
					var myreg = /^((0\d{2,3}-\d{7,8})|(1[34578]\d{9}))$/;
					if(_self.index == ""){
						uni.showToast({
							title:"请选择企业类型",
							icon:"none"
						})
						return false
					}else if(_self.name == ""){
						uni.showToast({
							title:"请输入公司名称",
							icon:"none"
						})
						return false
					}
					
					else if(reg.test(_self.network)){
						uni.showToast({
							title:"官网不允许输入中文",
							icon:"none"
						})
						return false
					} 
					else if(_self.sketch == ""){
						uni.showToast({
							title:"请输入简介",
							icon:"none"
						})
						return false
					}
					else if(_self.smallPrimary == ""){
						uni.showToast({
							title:"请输入简介",
							icon:"none"
						})
						return false
					}
					else if(_self.phone == ""){
						uni.showToast({
							title:"请输入联系方式",
							icon:"none"
						})
						return false
					}else if(!myreg.test(_self.phone)){
							uni.showToast({
							title:"联系电话格式不正确",
							icon:"none"
						})
						return false
					}
					uni.request({
						url:_self.$api+"dockingManager/tradeUpdate", 
						data:{id:uni.getStorageSync('tradeId'),name:_self.name,sketch:_self.sketch,phone:_self.phone,address:_self.address,branch:_self.pull,network:_self.network,nots:_self.nots,upperId:uni.getStorageSync('componyId'),userName:_self.userName,camp:_self.camp,primary:_self.primary,shrink:_self.primary,smallPrimary:_self.smallPrimary,userPost:_self.job,userName:_self.contract_name}, 
						method:"GET",
						success:function(res){ 
							if(res.data ==98){
								uni.showToast({
									title:"您尚未认证",
									icon:'none'
								})
								return false
							}else if(res.data ==99){
								uni.showModal({
								title: '提示',
								content: '此操作需用户授权，是否进行授权',
								success: function (res) {
									if (res.confirm) {
										//跳转到授权页面  
										uni.navigateTo({
											url:"../../login/login"  
										})
										console.log('用户点击确定');
									} else if (res.cancel) {
										console.log('用户点击取消');
									}
								}
							});
							
								return false
							}else if(res.data ===0){
									uni.showToast({
									title:"修改失败"
							})
								return false
							}else if(res.data ==1){
									uni.showToast({
									title:"修改成功",
									success:function(){
										setTimeout(function(){
											uni.navigateBack({
												delta: 1,
												animationType: 'pop-out',
												animationDuration: 100
											});
										},1000)
									}
								})
								return false
							}else{
								uni.showToast({
									title:"系统异常"
								})
								return false
							}
							console.log(res)
						} 
						
					})
				},
				/**
				 * 上传图片
				 */
				chooseImage: async function() {
					var _self = this
						uni.chooseImage({
							success: (res) => {
								var tempFilePaths = res.tempFilePaths;
								uni.uploadFile({
									url: _self.$api+'dockingManager/upload', //仅为示例，非真实的接口地址
									//url: "http://www.tp5.com/index", //仅为示例，非真实的接口地址
									filePath: tempFilePaths[0],
									name: 'file', 
									formData: {
										'user': 'test'
									},
									success: (uploadFileRes) => {
										_self.primary = uploadFileRes.data.replace(/\"/g, "")
										console.log(uploadFileRes)
									}
								  }); 
							} 
						})
					},
					chooseImage1: async function() {
						var _self = this
							uni.chooseImage({
								success: (res) => {
									var tempFilePaths = res.tempFilePaths;
									uni.uploadFile({
										url: _self.$api+'dockingManager/upload', //仅为示例，非真实的接口地址
										//url: "http://www.tp5.com/index", //仅为示例，非真实的接口地址
										filePath: tempFilePaths[0],
										name: 'file', 
										formData: {
											'user': 'test'
										},
										success: (uploadFileRes) => {
											_self.shrink = uploadFileRes.data.replace(/\"/g, "")
											console.log(uploadFileRes)
										}
									  }); 
								} 
							})
						},
				/**
				 * 需求更多
				 */
				gotoSupplyMore:function(){
					var _self = this
					uni.navigateTo({
						url:"../myComponyNeed/myComponyNeed?id="+_self.tradeId
					})
				},
				/**
				 * 供应更多 
				 */
				gotoSupplyMore1:function(){
					var _self = this
					uni.navigateTo({
						url:"../myComponyApply/myComponyApply?id="+_self.tradeId
					})
				},
				/**
				 * 产品服务
				 */
				getProduct:function(){
					var _self = this
					uni.request({
						url:_self.$api+"dockingManager/totalNewQuery",
						data:{pull:18,id:0,optionId:uni.getStorageSync("openId"),branch:0,trade:_self.tradeId},
						method:"GET",
						success:function(res){
							_self.productList = res.data.slice(0,2) 
							console.log(res)
						}
					})
				},
				gotoSupplyMore2:function(mark,branch,part_name){
					var _self = this
					uni.navigateTo({
					url:"../../pageChildren/componyProduct/componyProduct?mark="+mark+'&branch='+branch+'&part_name='+part_name
						//url:"../myComponyProduct/myComponyProduct?mark="+mark+'&branch='+branch+'&part_name='+part_name
					})
				},
				goInfoDetail:function(id,mark){
					var _self = this
					uni.navigateTo({
						url:"../myComponyNeedDetail/myComponyNeedDetail?id="+id+"&mark="+mark+"&tradeId="+_self.tradeId
					})
				},
				goProductDetail:function(id,branch,mark,part_name){
					var _self = this
					uni.navigateTo({
						url:"../../pageChildren/productDetail/productDetail?id="+id+"&branch="+branch+"&part_name="+part_name+'&mark='+mark
					})
				},
				/**
				 * 模块查询
				 */
				getDataList:function(){
					var _self = this
					uni.request({
						url:_self.$api+"dockingManager/titleBranchQuery",
						data:{optionId:uni.getStorageSync("openId"),id:uni.getStorageSync('tradeId')},
						method:"GET",
						success:function(res){
							_self.dataList = res.data
							console.log(res)
						}
					})
					
				},
				/**
				 * 我的同事
				 */
				getMyFriend:function(){
					var _self = this
					uni.request({
						url:_self.$api+"dockingManager/tradeWorkQuery",
						data:{optionId:uni.getStorageSync("openId"),branch:1,live:4},
						method:"GET",
						success:function(res){
							_self.friendList = res.data
							console.log(res)
						}
					})
				},
				gotoManager:function(){
					var _self = this
					uni.navigateTo({
						url:"../myFriends/myFriends"
					})
				},
				/**
				 * 操作
				 */
				prompt:function(){
				this.$refs.prompt.show();
			},
			onConfirm:function(e){
				var _self = this
				console.log(e);
				let _cost = e;
				if (_cost == '') {
				 uni.showToast({
				 	'title':'请输入服务模块名称',
					'icon':'none'
				 })
				 console.log('你还未输入');
				 return;
				}
				else{  
				  uni.showModal({
				  	title: '提示',
				  	content: '是否新增服务模块:'+_cost, 
				  	showCancel: false,
				  	confirmText: "确定",
					success:function(){
						uni.request({
							url:_self.$api+'dockingManager/titleBranchAdd',
							data:{id:uni.getStorageSync('tradeId'),name:_cost},
							success:function(res){
								if(res.data == 1){
									 uni.showToast({
										'title':'新增成功',
									})
									_self.$refs.prompt.hide();
									_self.getInfo()
									return
								}else{
									 uni.showToast({
										'title':'新增失败，请联系工作人员',
									})
									_self.$refs.prompt.hide();
									return false
								}
							}
						})
					}
				  })
				}
			},
			onCancel:function(){
				this.$refs.prompt.hide();
				this.$refs.prompt.cost = '';
			},
			/**
			* 跳转发布内容
			*/
		   goServerContent:function(id){
			   uni.navigateTo({
			   	url:'../serverContent/serverContent?id='+id
			   })
		   },
		   /**
			* 删除分类
			*/
		   deleteContent:function(id){
			   var _self = this
			   uni.showModal({
				title: '提示',
				content: '是否删除此分类',
				success: function (res) {
						if (res.confirm) {
							uni.request({
								url:_self.$api+'dockingManager/titleBranchDelete',
								data:{id:id},
											success:function(res){
												if(res.data==1){
													uni.showToast({
														title:'删除成功'
													})
													_self.getInfo()
													return
												}else{
													uni.showToast({
														title:'删除失败',
														icon:'none'
													})
													return
												}
											}
							})
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});  
		   }
			

		},
		 components: {uniCollapse,uniCollapseItem,prompt,avatar},
		 computed: {
		  
		 },
		 filters:{
			 getPic:function(res){
			 	return res.split(',')[0]
			 },
			 getTime:function(res){
			 	return res.slice(0,11)
			 },
			 check_zh:function(obj){ 
			   return obj.replace(/[\u4E00-\u9FA5]|[\uFE30-\uFFA0]/g,'')
			 },
		 },
		 wacth:{
			
		 }
	}
